            "name": "Реінкорнація безробітного", 
            "description": "Буває в житті невдача. Тільки тридцятичотирирічний безробітний отаку-ледар доходить до точки і вирішує, що настав час взятися за розум і змінити своє нікчемне життя, як його на смерть збиває вантажівка. Однак на цьому історія його життя не закінчується, адже він приходить до тями в тілі дитини, причому в зовсім іншому світі, де в ходу магія та й лицарські бої на мечах цілком у порядку речей. Відтепер нашого героя звуть Рудеус Грейрат, і він почне таки життя з нового аркуша, але тепер уже в новому повному небезпеці та чудес світі.", 
            "author": "Ріфудзін на Магоноте",
            image: '/img/manga/4.jpg'
