            name: `Як ми це бачимо`,
            description: `Берджер зосереджується на тому, як люди бачать твори мистецтва, і на прикладі живопису та реклами показує, що разом із сюжетним наповненням зображення мають ідеологічний заряд, а засоби репродукування впливають на наше нинішнє ставлення до мистецьких творів.`,
            author: `Джордж Берджер`,
